import React from 'react';

import CitiesUtil from '../utils/citiesutil.js';

export default class TabNetCities extends React.Component {
	
	getCityLabel(city){
		let arrValue = city.split('|');
		if('' + this.props.mode === '2'){
			return arrValue[1] + ' ' + arrValue[0];
		}
		return arrValue[0];
	}
	
	render(){
		let cities = CitiesUtil.getCitiesArray(this.props.stateUf);
		let thisThis = this;
		return (
	    		<div className="field" style={this.props.style}>
		    		<label htmlFor={this.props.id}
		    		       title={this.props.title}>
		    			{this.props.label}
		    		</label><br/>
					<select id={this.props.id}
						    data-size="5"
						    className="form-control">
						{
							cities.map(function(city,index){
								return (
									<option key={index} 
								            value={city.split('|')[1]}>
										{thisThis.getCityLabel(city)}
									</option>
								);
		                    })
		                }
					</select>
				</div>
	    );
	}
    
}