import React from 'react';

const topYears = (useTopYears) => {
	let topyears = [];
	if(useTopYears !== 'true'){
		return topyears;
	}
	let max = new Date().getFullYear();
	let min = 2018;
	for(let i = max; i > min; i--){
		topyears = [...topyears,[i,i]];
	}
	return topyears;
};

const bottomYears = (useTopYears,mode) => {
	let bottomyears = [];
	if(!useTopYears){
		return bottomyears;
	}
	let max = 1995;
	let min = ('' + mode) === '1' ? 1984 : 1985;
	for(let i = max; i > min; i--){
		bottomyears = [...bottomyears,[i,i]];
	}
	bottomyears = [...bottomyears,[8888,'No se Aplica']];
	bottomyears = [...bottomyears,[9999,'Sem Informao']];
	return bottomyears;
};

export default function YearFilter(props){
    return (
    		<div className="field" style={props.style}>
	    		<label htmlFor={props.id} title={props.title}>{props.label}</label><br/>
	    		<select id={props.id}
	    			    data-size="5"
	    			    className="form-control">
		    		{
		    			topYears(props.useTopYears).map(function(year,index){
							return (
								<option key={index} 
							            value={year[0]}>
									{year[1]}
								</option>
							);
	                    })
	                }
					<option value="2018">2018</option>
					<option value="2017">2017</option>
					<option value="2016">2016</option>
					<option value="2015">2015</option>
					<option value="2014">2014</option>
					<option value="2013">2013</option>
					<option value="2012">2012</option>
					<option value="2011">2011</option>
					<option value="2010">2010</option>
					<option value="2009">2009</option>
					<option value="2008">2008</option>
					<option value="2007">2007</option>
					<option value="2006">2006</option>
					<option value="2005">2005</option>
					<option value="2004">2004</option>
					<option value="2003">2003</option>
					<option value="2002">2002</option>
					<option value="2001">2001</option>
					<option value="2000">2000</option>
					<option value="1999">1999</option>
					<option value="1998">1998</option>
					<option value="1997">1997</option>
					<option value="1996">1996</option>
					{
		    			bottomYears(props.useTopYears,props.mode).map(function(year,index){
							return (
								<option key={index} 
							            value={year[0]}>
									{year[1]}
								</option>
							);
	                    })
	                }
				</select>
			</div>
    );
}