import React from 'react';

import CitiesUtil from '../utils/citiesutil.js';

export default class TabNetStates extends React.Component {
	
	renderPre(option){
		if('' + this.props.mode === '1' && option === 1){
			return <option value="todosho_sp">Todos os Estados</option>;
		}
		if('' + this.props.mode === '1' && option === 2){
			return <option value="todosho">Todos os Estados Exceto SP</option>;
		}
		if('' + this.props.mode === '2' && option === 1){
			return <option value="">Todos os Estados</option>;
		}
		return null;
	}
	
	renderPos(option){
		if('' + this.props.mode === '2' && option === 1){
			return <option value="99">Sem Informao</option>;
		}
		if('' + this.props.mode === '2' && option === 2){
			return <option value="EX">Estrangeiro</option>;
		}
		return null;
	}
	
	render(){
		let states = CitiesUtil.getStatesArray();
		return (
	    		<div className="field" style={this.props.style}>
		    		<label htmlFor={this.props.id} 
		    		       title={this.props.title}>
		    			{this.props.label}
		    		</label><br/>
					<select id={this.props.id}
						    data-size="5"
						    onChange={() => this.props.onChange()}
						    className="form-control">
						{ this.renderPre(1) }
						{ this.renderPre(2) }
						{
							states.map(function(state,index){
								return (
									<option key={index} 
								            value={CitiesUtil.toUfPipe(state)}>
										{CitiesUtil.clearStateName(state)}
									</option>
								);
		                    })
		                }
						{ this.renderPos(1) }
						{ this.renderPos(2) }
					</select>
				</div>
	    );
	}
	
}