import React from 'react';

import BigButton from './elements/bigbutton.js';
import TabNetSearchScreen from './tabnetsearch.screen.js';


export default class MainScreen extends React.Component {
	
	constructor(props) {    
		super(props);    
		this.state = {      
			view: 'initial'
		};  
	}
	
	setView(name){
		this.setState({view: name});
	}
	
	search(){
		this.setView('tabNetSearch');
	}
	
	download(){
		this.setView('tabNetDowload');
	}
	
	timeIntervalSearch(){
		this.setView('tabNetTimeInterval');
	}
	
    renderinitialView() {
	    return (
	    		<div className="container">
		    		<div className="row justify-content-center" 
		    			 style={{'paddingTop': '3em'}}>
		    			<BigButton label="Consultar" 
		    				       className="fab fa-searchengin" 
		    				       onClick={() => this.search()} />
		    			<BigButton label="Download" 
		    				       className="fas fa-download" 
		    				       onClick={() => this.download()} />
				    	<BigButton label="Relatrio de Tempo" 
		    				       className="fas fa-history" 
		    				       onClick={() => this.timeIntervalSearch()} />
					</div>
				</div>
	    );
    }
    
    renderTabNetTimeIntervalView(){
    	return (
    			<div className="container">
		    		<div className="row justify-content-center" 
		    			 style={{'paddingTop': '3em'}}>
		    			history: { this.state.view }
		    		</div>
		    	</div>
    	);
    }
    
    render(){
    	if(['tabNetSearch','tabNetDownload'].includes(this.state.view)){
    		return <TabNetSearchScreen />;
    	}
    	if('tabNetTimeInterval' === this.state.view){
    		return this.renderTabNetTimeIntervalView();
    	}
    	return this.renderinitialView();
    }
    
}