import React from 'react';

export default function DrugFilter(props){
    return (
    		<div className="field" style={props.style}>
	    		<label htmlFor={props.id} title={props.title}>{props.label}</label><br/>
	    		<select id={props.id}
	    			    data-size="5"
	    			    className="form-control">
					<option value="">Todas</option>
					<option value="1">Nunca</option>
					<option value="2">Ex-consumidor</option>
					<option value="3">Sim</option>
					<option value="4">No avaliado</option>
					<option value="8">No se aplica</option>
					<option value="9">Sem Informao</option>
				</select>
			</div>
    );
}