const CitiesUtil = {
	getStatesArray(){
		let statess = [];
		let size = cities.length;
		for(let i = 0; i < size; i++){
			let statee = cities[i][1] + '|' + cities[i][0];
			if(statess.includes(statee)){
				continue;
			}
			statess = [...statess,statee];
		}
		return statess.sort();
	},
	getCitiesArray(stateUf){
		let uf = stateUf.split('|')[1];
		let stateCode = '' + CitiesUtil.getNumericUfCode(uf);
		let citiess = [];
		let size = cities.length;
		for(let i = 0; i < size; i++){
			if(cities[i][0] !== stateCode){
				continue;
			}
			let city = cities[i][3] + '|' + cities[i][2];
			if(citiess.includes(city)){
				continue;
			}
			citiess = [...citiess,city];
		}
		citiess = citiess.sort();
		citiess = ['Todas as Cidades|',...citiess];
		return citiess;
	},
	getNumericUfCode(ufCode){
		let size = ufCodes.length;
		for(let i = 0; i < size; i++){
			if(ufCodes[i][1] === ufCode){
				return ufCodes[i][0];
			}
		}
		return 0;
	},
	getUfCode(numericCode){
		let size = ufCodes.length;
		for(let i = 0; i < size; i++){
			if(('' + ufCodes[i][0]) === numericCode){
				return ufCodes[i][1];
			}
		}
		return '00';
	},
	toUfPipe(name){
		return 'uf|' + CitiesUtil.getUfCode(name.split('|')[1]);
	},
	clearStateName(name){
		return name.split('|')[0];
	}
};

export default CitiesUtil;

























